<template>
<div class='twitchWrapper'>
	<ul>
		<li v-for="stream in liveStreamList"><a v-on:click='setTargetStream(stream.display_name)'>{{stream.display_name}} <span v-if='stream.live'> - live ({{stream.live.viewers}})</span></a></li>
	</ul>
	<div class="wrapper">
		<div class='twitchStream'>
			<iframe :src="targetStreamUrl" frameborder="0" scrolling="no"></iframe>
		</div>
		<div class='twitchChat'>
			<iframe frameborder="0" scrolling="no" :src="targetChatUrl"></iframe>
		</div>
	</div>
</div>
</template>

<script>
// import that global data store
import { mapGetters, mapActions } from 'vuex'

export default {
	name: 'twitch-embed',
	data () {
		return {
			targetedStream: 'lowco2525'
		}
	},
	methods: {
		setTargetStream: function (channel) {
			this.targetedStream = channel
		},
		...mapActions([
			'updateChannelInfoList'
		])
	},
	computed: {
		targetStreamUrl () {
			return 'https://player.twitch.tv/?channel=' + this.targetedStream
		},
		targetChatUrl () {
			return 'https://www.twitch.tv/' + this.targetedStream.toLowerCase() + '/chat'
		},
		// return a list of the live streams in a proper order
		liveStreamList () {
			var liveCollection = []
			var offlineCollection = []
			// Sort the collections into online and offline
			for (var channel in this.$store.state.TwitchInfo) {
				if (this.$store.state.TwitchInfo[channel].live) {
					liveCollection.push(this.$store.state.TwitchInfo[channel])
				} else {
					offlineCollection.push(this.$store.state.TwitchInfo[channel])
				}
			}
			// sort the live streams
			liveCollection.sort(function (a, b) {
				return b.live.viewers - a.live.viewers
			})
			// then tack on the remainders
			return liveCollection.concat(offlineCollection)
		},
		...mapGetters([
			'twitchInfo'
		])
	},
	mounted () {
		this.updateChannelInfoList().then(function () {
			this.targetedStream = this.liveStreamList[0].display_name
		}.bind(this))
	}
}
</script>

<style>
.twitchWrapper {
	position: relative;
	background-color: #353535;
}

.twitchStream {
	position: relative;
	width:75%;
}

.twitchStream:before{
	content: "";
	display: block;
	padding-top: 56.25%; /* 16:9 */
}

.twitchChat{
	position: absolute;
	top: 0;
	bottom: 0;
	right: 0;
	width: 25%;
}

.twitchStream iframe, .twitchChat iframe {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}
.wrapper{
	display: block;
	position: relative;
}

li {
	display: inline-block;
	margin: 10px;
	cursor: pointer;
}

ul {
	display: block;
	list-style-type: none;
	margin: 0;
}

@media (max-width: 1024px) {
	.twitchStream{
		width:100%;
	}
	.twitchChat{
		display:none;
	}
}
</style>



