import Vue from 'vue'
import Vuex from 'vuex'
import Twitch from '../api/twitch.js'
import config from '../../config/config.json'

Vue.use(Vuex)

// root state object.
// each Vuex instance is just a single state tree.
const state = {
	TwitchInfo: {}
}

// mutations are operations that actually mutates the state.
// each mutation handler gets the entire state tree as the
// first argument, followed by additional payload arguments.
// mutations must be synchronous and can be recorded by plugins
// for debugging purposes.
const mutations = {
	updateTwitchState (state, twitchResult) {
		state.TwitchInfo = twitchResult
	},
	updateLiveStreamsList (state, twitchResult) {
		state.TwitchInfo[twitchResult._id].live = twitchResult
	}
}

// actions are functions that causes side effects and can involve
// asynchronous operations.
const actions = {
	// Updates the live status for each channel in the data store Needs work still
	updateLiveStreamsList ({ dispatch, commit }) {
		var p1 = new Promise(function (resolve, reject) {
			dispatch('updateChannelInfoList').then(function () {
				console.log(state.TwitchInfo)
				if (!(Object.keys(state.TwitchInfo).length === 0 && state.TwitchInfo.constructor === Object)) {
					console.log('it did exist')
					var promises = []
					for (var property in config.team_members) {
						promises.push(
							Twitch.isStreamLive(config.team_members[property].id)
						)
					}
					Promise.all(promises).then(function (promise) {
						promise.forEach(function (result) {
							if (result.body.stream) {
								commit('updateLiveStreamsList', result.body.stream)
							}
						})
						resolve(state)
					})
				} else {
					console.log('it didnt exist')
					dispatch('updateChannelInfoList')
				}
			})
		})
		return p1
	},
	// Updates the entire twitch data store
	updateChannelInfoList ({ commit }) {
		var p1 = new Promise(function (resolve, reject) {
			var promises = []
			for (var property in config.team_members) {
				promises.push(
					Twitch.getChannelInfo(config.team_members[property].id)
				)
			}
			Promise.all(promises).then(function (promise) {
				var newState = {}
				var livePromises = []
				// Collect all the channel info and put it into our new twitch state
				promise.forEach(function (result) {
					if (result.body) {
						newState[result.body._id] = result.body
						newState[result.body._id].live = null
					}
				})
				// For each channel in the new state we check to see if it's live or not
				for (var property in config.team_members) {
					livePromises.push(
						Twitch.isStreamLive(config.team_members[property].id)
					)
				}
				Promise.all(livePromises).then(function (result) {
					for (var channel in result) {
						if (result[channel].body.stream) {
							newState[result[channel].body.stream.channel._id].live = result[channel].body.stream
						}
					}
					console.log(newState)
					commit('updateTwitchState', newState)
					resolve()
				})
			})
		})
		return p1
	}
}

// getters are functions
const getters = {
	twitchInfo: state => { return state.twitchInfo }
}

// A Vuex instance is created by combining the state, mutations, actions,
// and getters.
export default new Vuex.Store({
	state,
	getters,
	actions,
	mutations
})



