<template>
	<div class="members">
		<section-header :sectionName="sectionName"></section-header>
		<div class="streamer-list-wrapper">
			<member-tile v-for="stream in streamList" :stream='stream'></member-tile>
		</div>
	</div>
</template>

<script>
import { mapActions } from 'vuex'
import SectionHeader from './SectionHeader'
import MemberTile from './MemberTile'

export default {
	name: 'members',
	data () {
		return {
			streamList: {},
			sectionName: 'MEMBERS'
		}
	},
	components: {
		SectionHeader,
		MemberTile
	},
	methods: {
		...mapActions([
			'updateChannelInfoList'
		])
	},
	mounted () {
		this.updateChannelInfoList().then(function () {
			this.streamList = this.$store.state.TwitchInfo
		}.bind(this))
	}
}
</script>

	<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.streamer-list-wrapper{
	margin: 0 auto;
  padding-bottom: 20px;
	max-width: 1200px;
}
.yellow{
	color: #fbcb17;;
}
.members{
	background-color: #4f5154;
	text-align: left;
}
</style>


