<template>
	<div id="nav-wrapper">
		<div id="gold-header" v-show="showGold">
		</div>
		<nav id="nav-bar">
			<div class='logo'>
			<!-- ADD LINK THAT TRIGGERS ELEVATOR JS OR RETURNS TO THE HOME PAGE -->
				<a href="./"><img src="../assets/logo.png" height="50px;"></a>
			</div>
			<ul class="menu">
				<li><a href="https://www.twitch.tv/team/catalyst/"><span>TWITCH</span></a></li>
				<li><a href="./news/"><span>NEWS</span></a></li>
				<li><a href="./streams"><span>STREAMS</span></a></li>
				<li><a href="./about"><span>ABOUT US</span></a></li>
			</ul>
		</nav>
	</div>
</template>

<script>
export default {
	name: 'header-bar',
	props: ['showGold']
}
window.onscroll = function () {
	var scrollTop = (window.pageYOffset !== undefined) ? window.pageYOffset : (document.documentElement || document.body.parentNode || document.body).scrollTop
	if (scrollTop < 400) {
		document.getElementById('gold-header').style['margin-bottom'] = '0'
		document.getElementById('nav-bar').style.position = 'initial'
	} else if (scrollTop >= 400 && scrollTop < 600) {
		document.getElementById('nav-bar').style.top = -100
	} else {
		document.getElementById('nav-bar').style.position = 'fixed'
		document.getElementById('gold-header').style['margin-bottom'] = '70px'
		document.getElementById('nav-bar').style.top = 0
	}
}
</script>

	<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
a{
	text-decoration: none;
	color: #fbcb17;
}
span{
	padding: 10px;
	line-height: 45px;
	border-radius: 10px;
	cursor: pointer;
	transition: all .1s ease-in-out;
}
#nav-bar{
	position: initial;
	text-align: center;
	transition: all .5s ease-in-out;
	width: 100%;
	background-color: #4f5154;
	top: -100;
}
#gold-header{
	height: 120px;
	background-color: #fbcb17;
}
.logo{
	display: inline-block;
	vertical-align: middle;
	margin: 5px auto;
}
.menu{
	display: inline-block;
	position: absolute;
	right: 0;
	padding-right: 5%;
	font-size: 14px;
	-webkit-margin-before: 0;
	-webkit-margin-after: 0;
	-webkit-margin-start: 0;
	-webkit-margin-end: 0;
	-webkit-padding-start: 0;
}
span:hover{
	background-color: #fbcb17;
	color: #4f5154;
}
</style>


