import Vue from 'vue'

export default {
	getTwitchId (username) {
		var url = 'https://api.twitch.tv/kraken/users?login=' + username
		var p1 = new Promise(
		function (resolve, reject) {
			Vue.http.get(url).then(response => {
				console.log(response)
				resolve(response)
			}, response => {
				// error goes here
				console.log(response)
				reject(response)
			})
		})
		return p1
	},
	isStreamLive (channelId) {
		var url = 'https://api.twitch.tv/kraken/streams/' + channelId
		var p1 = new Promise(function (resolve, reject) {
			Vue.http.get(url).then(response => {
				resolve(response)
			}, response => {
				// error goes here
				reject(response)
			})
		})
		return p1
	},
	getChannelInfo (channelId) {
		var url = 'https://api.twitch.tv/kraken/channels/' + channelId
		var p1 = new Promise(function (resolve, reject) {
			Vue.http.get(url).then(response => {
				resolve(response)
			}, response => {
				// error goes here
				reject(response)
			})
		})
		return p1
	}
}



