<template>
	<a :href="stream.url">
		<div class="member-tile">
			<img :src="stream.logo">
			<div class="info-section">
				<h2>{{stream.display_name}}</h2>
				<p>{{description}}</p>
				<h2>{{stream.url.slice(12)}}<span v-show=stream.live> - live</span></h2>
			</div>
		</div>
	</a>
</template>

<script>
import config from '../../../config/config.json'
export default {
	name: 'member-tile',
	computed: {
		description: function () {
			return config.team_members[this.stream.display_name.toLowerCase()].description
		}
	},
	props: ['stream']
}
</script>

	<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
img{
	border-radius: 100px;
	width: 80px;
	border: 2px solid #dedede;
	padding: 3px;
	margin-right: 25px;
}
h2{
	color: #fbcb17;
	font-size: 16px;
	margin: 0;
}
p{
	margin: 5px 0;
}
.member-tile{
	display: inline-block;
	width: 500px;
	margin: 10px 25px;
}
.info-section{
	display: inline-block;
	vertical-align: top;
	padding-top: 7px;
	text-align: left;
	max-width:248px;
}
</style>


